<template>
  <div class="header">
    <div class="header__inner">
      <div class="header__navgroup">
        <div class="header__navitem">
          <a href="/">HOME</a>
        </div>
        <div class="header__navitem">
          <a href="https://twitter.com/Premier_XIV" target="_blank">Twitter</a>
        </div>
      </div>
    </div>
  </div>
  <div class="content">
    <div class="fontblack title TextTyping">StarConnection</div>
    <div class="wrapper">
      <div class="flex justify-center mt-4">
        <form action="#" class="search-form-001">
          <label>
            <input type="text" v-model="link" placeholder="UID" />
          </label>
          <button @click="validateAndNavigate" class="btn btn--orange"></button>
        </form>
      </div>
      <p class="flex justify-center mt-4 fontblack">Powered by mihomo.me</p>
    </div>
  </div>
  <footer class="footer">
    <div class="footer-container">
      <p class="text-center fontblack">Copyright © 2023 - Created by Premier</p>
    </div>
  </footer>
</template>

<script>
export default {
  data() {
    return {
      link: "", // Holds the entered link
    };
  },
  computed: {
    isValidUid() {
      // UIDが9桁かつ数字であるかをチェック
      return /^\d{9}$/.test(this.link);
    },
    formattedLink() {
      return "/u/" + this.link;
    },
  },
  methods: {
    validateAndNavigate() {
      if (this.isValidUid) {
        this.$router.push(this.formattedLink);
      } else {
        // UIDが9桁かつ数字でない場合の警告処理
        window.alert("UIDは9桁の数字で入力してください。");
      }
    },
  },
};
</script>
